html,
body {
  margin: 0;
  padding: 0;
}

body {
  color: black;
  font-family: 'Ubuntu', sans-serif;
  line-height: 2;
}

main {
  min-height: 100vh;
}

.link {
  text-decoration: none;
}

.link:link,
.link:active,
.link:visited {
  font-weight: bold;
  color: rgb(0, 0, 0);
}

a:hover {
  text-decoration: underline;
}

strong {
  font-weight: bold;
}

.container {
  margin: 0 1rem;

  @media(min-width: 80rem) {
    margin: 0 10rem;
  }
}

.row {
  display: grid;
  grid-auto-flow: column;
  gap: 2rem;
}

.row-end {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.col {
  display: block;
}

.logo {
  height: 1.4rem;
}

.block {
  padding: 6rem 0;
}

.block-header {
  position: relative;

  margin-top: -6rem;
  margin-bottom: 6rem;
  padding: 6rem 0;

  .heading-1,
  .heading-2,
  .heading-3,
  .heading-4,
  .heading-5,
  .heading-6 {
    margin: 0;
  }

  .permalink {
    display: none;
  }

  &:hover > .permalink {
    display: block;
  }

  &::before {
    position: absolute;
    z-index: -1;

    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    opacity: 0.35;

    content: '';
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }
}

.block-heading {
  width: max-content;
  padding: 0.5rem 2rem;

  background: rgb(255, 222, 0);

  font-size: 2rem;
  font-weight: bold;
}

.block:nth-child(even) {
  background: rgb(102, 102, 102);
  color: white;

  .link {

    &,
    &:link,
    &:visited {
      color: rgb(255, 222, 0);
    }
  }

  .para {
    color: white;
  }

  .preamble {
    color: white;
  }
}

.block-large {
  box-sizing: border-box;


  @media (orientation: landscape) {
    height: 100vh;
  }
}

.block-center {
  > .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.heading-huge {
  font-size: 3rem;
  font-weight: bold;
}

.markdown h1,
.markdown h2,
.markdown h3,
.markdown h4,
.markdown h5,
.markdown h6,
.heading-1,
.heading-2,
.heading-3,
.heading-4,
.heading-5,
.heading-6 {
  margin-top: 2rem;
  margin-bottom: 2rem;
  line-height: 1;

  &:first-child {
    margin-top: 0;
  }
}

.markdown h1,
.heading-1 {
  font-size: 2rem;
  font-weight: bold;
}

.markdown h2,
.heading-2 {
  &:first-child {
    margin-top: 0;
  }

  font-size: 1.4rem;
  font-weight: bold;
}

.markdown h3,
.heading-3 {
  font-size: 1.2rem;
}

.ul {
  margin: 1rem 0;
  list-style-type: disc;
  list-style-position: inside;

  > li {
    margin: 0.5rem 0;
    margin-left: 1rem;
  }
}

.article {
  margin: 2rem 0;

  &:first-child {
    margin-top: 0;
  }
}

.markdown p,
.para {
  max-width: 50rem;
  margin: 1rem 0;
  color: rgb(75, 75, 80);
}

.preamble {
  max-width: 50rem;
  margin: 2rem 0;
  font-size: 1.2rem;
}

.signature {
  margin: 1rem 0;
  line-height: 1.5;
  color: rgb(75, 75, 80);
}

.handwriting {
  color: rgb(0, 0, 0);

  font-family: 'Ms Madi', serif;
  font-weight: 100;
  font-size: 2em;
}

.note {
  margin: 2rem 0;
  padding: 1rem;

  background: rgb(245, 245, 245);

  border: 1px solid rgb(200, 200, 200);
  border-radius: 0.2rem;
}

.screenshot {
  display: inline-block;
  box-sizing: border-box;
  width: calc(100vw - 2rem);
  overflow: hidden;

  border-radius: 3px;
  border: 1px solid rgb(0, 0, 0);
  box-shadow: 0 0 5px 4px rgba(130, 130, 130, 0.25);

  @media(min-width: 40rem) {
    width: 50vw !important;
  }
}

.huge-list {
  display: grid;
  grid-auto-flow: row;
  gap: 4rem;

  margin: 4rem 0;

  @media(min-width: 80rem) {
    grid-template-columns: repeat(2, 1fr);
  }

  > li {
    align-self: stretch;
    justify-self: stretch;
    display: flex;
    flex-direction: row;
  }

  > li > i {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;

    font-size: 4rem;

    color: rgb(255, 222, 0);
  }
}

.meta {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: max-content;
  align-items: center;
  gap: 1rem;

  margin: 1rem 0;

  line-height: 1;

  font-size: 0.9rem;

  vertical-align: baseline;
}

.tag-list {
  vertical-align: baseline;

  > li {
    vertical-align: baseline;
    display: inline-block;
    padding: 0.3rem;

    background: rgb(255, 222, 0);
    color: black;

    border-radius: 0.2rem;
  }
}

.tag-list-white {
  > li {
    background: white;
    color: rgb(0, 100, 206);
  }
}

.dl {
  margin: 1rem 0;

  display: grid;
  grid-template-columns: max-content auto;
  gap: 0 1rem;

  > dt {
    grid-column: 1 / 1;
    font-weight: bold;
  }

  > dd {
    grid-column: 2 / 2;
  }
}

article + article {
  margin-top: 5rem;
}
