.site-footer {
  padding: 4rem 2rem;

  color: black;
  background: rgb(255, 222, 0);

  @media(min-width: 80rem) {
    padding: 4rem 10rem;
  }
}

.site-footer-columns {
  display: grid;
  grid-template-columns: repeat(1, auto);
  gap: 4rem;

  @media(min-width: 60rem) {
    grid-template-columns: repeat(2, auto);
  }

  @media(min-width: 80rem) {
    grid-template-columns: repeat(3, auto);
  }

  @media(min-width: 100rem) {
    grid-template-columns: repeat(4, auto);
  }
}

.footer-header {
  margin-top: 2rem;
  font-size: 1.5rem;
}

.footer-links {
  display: flex;
  flex-direction: column;
}

.footer-links>div {
  margin: 0.5rem 0;
}

.footer-links>div>a:hover {
  text-decoration: unset;
}

.footer-links>div>.link,
.footer-links>div>.link:link,
.footer-links>div>.link:active,
.footer-links>div>.link:visited {
  color: black;
  text-decoration: none;
}

.footer-links>div>.link:hover {
  text-decoration: underline;
}

.site-legal {
  display: grid;
  grid-template-columns: repeat(1, auto);
  gap: 1rem;

  padding: 2rem;

  font-size: 0.9rem;

  @media(min-width: 80rem) {
    padding: 2rem 10rem;
    grid-template-columns: repeat(2, auto);
  }
}

.copyright {
  text-align: center;

  @media(min-width: 60rem) {
    text-align: left;
  }
}

.legal-links {
  display: grid;
  grid-auto-flow: column;
  gap: 1rem;
  justify-content: center;

  @media(min-width: 60rem) {
    justify-content: end;
  }

  .link:link,
  .link:active,
  .link:visited {
    color: inherit;
  }

  .link:hover {
    text-decoration: underline;
  }
}
